import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';
import PadItem from "./padItem";
const Nppad = () => {

    const [data1, setdata1] = useState();
    const [data2, setdata2] = useState([]);
    const [padd, setPadd] = useState('A');
    const Inevent=(e)=>{
            setdata1(e.target.value);
            console.log(e.target.value);
    }

    const in2 = () =>{
            setdata2((olditems)=>{

                return[...olditems, data1];
            });

            setdata1('');    
    }

    const deleteiteme =(id)=>{
        setdata2((olditems)=>{
            return(olditems.filter((arr, index)=>{
                return(index!==id);
            }))
        })
        
    }

    const kkk = (e) =>{
            console.log(e.target.value);
            setPadd(e.target.value);
            const id ="";
            setdata2((olditems)=>{
                return(olditems.filter((arr, index)=>{
                    return(index===id);
                }))
            })
    }
    return(

        <>

            <div className=" mx-auto pl-4 col-md-8 " >

            <div className=" col-sm-5 mx-auto" >
            <select className="col-sm-5 mb-4 form-control" onChange={kkk}>
                <option className="col-sm-5 mb-4 form-control" value="A">Notepad A</option>
                <option className="col-sm-5 mb-4 form-control" value="B">Notepad B</option>
            </select>
            
            <div style={{border:'2px solid grey',height:'38rem',backgroundColor:'#EBF30B',overflowX:'hidden',overflowY:'revert'}} className="pl-4 pr-4 pb-4 ">

                <input type="text" name='stdata' className="form-control col-sm-4 mt-4"  onChange={Inevent} value={data1} placeholder="Add items"/>

                <button onClick={in2} className="btn btn-danger mt-3  pl-5 pr-5" autoFocus='true' style={{marginLeft:'10rem'}}>submit</button>

            <div className="" style={{boxSizing:'border-box',width:'auto',margin:'auto'}}> 
          

                {data2.map((b,i)=>{
                    return(
                        <>
                            <PadItem  onSelect={deleteiteme} pad={padd} id={i} item={b}/>
                        </>
                    )
                })}
            
            </div>
            </div>
            </div>
</div>
        </>
    )
}

export default Nppad;