import React, { useState } from 'react';

const PadItem = (props) =>{
    
    const [hello, setHello] = useState();
    const [line, setline] = useState(false);
    
    const deleteitem=(e)=>{
        
        if(line==true){
            setline(false);
        }
        else {
            setline(true);        
        }
    }
    let value1 = props.id;
    value1=value1+1;
    let value = props.pad;
    console.log(value);
    
    if(props.pad==='A'){
                return(
                    <div className="d-flex">

                <table>
                <tr>
                
                    <td>
                    <h2>{value1}</h2>
                    </td>
                    
                    <td>
                    <ul >
                     <li onClick={deleteitem} id="through" style={{textDecoration: line ? 'line-through':'none',listStyleType:"none",fontSize:'35px'}}>{props.item}</li>
                    </ul>
                    </td>
                </tr>

                
                
                </table>
                </div>
                    
                )
            }
            else if(props.pad==='B'){
                return(
                    <><table>
                    <tr>
                    
                        <td>
                        <h2>{value1}</h2>
                        </td>
                        
                        <td>
                        <ul >
                        <li onClick={()=>{props.onSelect(props.id)}} style={{listStyleType:"none",fontSize:'35px'}}  >{props.item}</li>
    
                        </ul>
                        </td>
                    </tr>
    
                    
                    
                    </table>
                    
                    </>
                )
                }
        }
    

export default PadItem;


